@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);

header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
/* CTA */
.cta{
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}
/* HeaderSocial Icons */
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header_socials::after{
    content: '';
    width: 1px;
    background: var(--color-primary);
}
/* me Pic */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 11rem 12rem 1rem 1rem;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}
/* scroll down  */
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}
/* Media Queries */
/* Tablets */
@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }
}
/* Mobiles */
@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
    .header_socials, .scroll_down{
        display: none;
    }
}
nav{
    background: rgba(0, 0, 0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom:2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0, 0, 0,0.3);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-bg)
}
.about_container{
    display: grid;
    grid-template-columns: 100%;
    grid-gap: 15%;
    gap: 15%;
}
