.about_cards{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 0.5rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}
.about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}
.about_card h5{
    font-size: 0.95rem;
}
.about_card small{
    font-size: 0.7rem;
    color: var(--color-light);
}
.about_content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

/* Media Queries */
/* Tablets */
@media screen and (max-width: 1024px){
    .about_container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
    .about_me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about_content p{
        margin: 1rem 0 1.5rem;
    }

}
/* Mobiles */
@media screen and (max-width: 600px){
    .about_me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about_cards{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .about_content{
        text-align: center;
    }
    .about_content p {
        margin: 1.5rem 0;
    }  
    .btn{
        left: 0;
    }
}


.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}
.experience_container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 1rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.experience_container > div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.experience_container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}
.experience_details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}
.experience_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
    
}

@media screen and (max-width: 1024px) {
    .experience_container{
        grid-template-columns: 1fr;
    }
    .experience_container > div{
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
    
}

@media screen and (max-width: 600px){
    .experience_container{
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.experience_container > div {
    width: 100%;
    padding: 2rem 1rem;
} 
.portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}
.portfolio_item{
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 1rem;
    border: 1px solid transparent;
    transition: var(--transition);
}
.portfolio_item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;
}
.portfolio_item-image{
    border-radius: 0.3rem;
    border: solid;
    border-color: rgba(0, 0, 0, 0.106);
    overflow: hidden;
}
.portfolio_item h3{
    margin: 1.2rem 0 2rem;   
}
.portfolio_item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}
@media screen and (max-width: 1024px) {
    .portfolio_container{
        grid-template-columns: 1fr 1fr;
        grid-gap: 1.2rem;
        gap: 1.2rem;
    }
}
@media screen and (max-width: 600px){
    .portfolio_container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
} 

.container.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12%;
    gap: 12%;
}
.contact_options{
    display:flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}
.contact_option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 0.5rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}
.contact_option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}
.contact_option-icon{
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;   
}
input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.2rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}
/* Media Queries */
/* Tablets */
@media screen and (max-width: 1024px){
    .container.contact_container{
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/* Mobiles */
@media screen and (max-width: 600px){
    .container.contact_comtainer{
        width: var(--container-width-sm)
    }
} 

footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a {
    color: var(--color-bg);
}
.footer_logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin: 0 auto 6rem;
}

@media screen and (max-width: 600px){
    .permalinks{
        flex-direction: column;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
} 

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
:root{
  --color-bg: #46463e;
  --color-bg-variant: #505643;
  --color-primary: #d3ac63;
  --color-primary-variant:#557d7da3;
  --color-white: #8F7245;
  --color-light: rgba(255, 255, 255, 0.72);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}
/* #c 0b189 */
/* #B3AA99; */
/* #316069 */
/* rgba(77,181,255,0.4) */
html{
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}
body{
  font-family: 'Poppins', sans-serif;
  background: #46463e;
  background: var(--color-bg);
  color: #8F7245;
  color: var(--color-white);
  line-height: 1.7;
  /* background-image: url(../src/assests/bg-texture.png); */
}
/* Gen Style */
.container{
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}
h1,h2,h3,h4,h5{
  font-weight: 500;
}
h1{
  font-size: 2.5rem;
}
section{
  margin-top: 8rem;
}
section > h2, section > h5{
  text-align: center;
  color: rgba(255, 255, 255, 0.72);
  color: var(--color-light);
}
section > h2 {
  color: #d3ac63;
  color: var(--color-primary);
  margin-bottom: 3rem;
}
.text-light{
  color: rgba(255, 255, 255, 0.72);
  color: var(--color-light)
}
a{
  color: #d3ac63;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}
a:hover{
  color: #8F7245;
  color: var(--color-white);
}
.btn{
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #d3ac63;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.3rem;
  cursor: pointer;
  border: 1px solid #d3ac63;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}
.btn:hover{
  background: #8F7245;
  background: var(--color-white);
  color: #46463e;
  color: var(--color-bg);
  border-color: transparent;
}
.btn-primary{
  background: #d3ac63;
  background: var(--color-primary);
  color: #46463e;
  color: var(--color-bg);
}
img{
  display: block;
  width: 100%;
  object-fit: cover;
}
/*  MEDIA QUERIES ( Medium Tablets) */
@media screen and (max-width: 1024px) {
  .container{
    width: 86%;
    width: var(--container-width-md);
  }
  section{
    margin-top: 6rem;
  }
}

/*  MEDIA QUERIES ( Small Mobible Devices) */

@media screen and (max-width: 600px) {
  .container{
    width: 90%;
    width: var(--container-width-sm);
  }
  section > h2 { 
    margin-bottom: 2rem;
  }
}
 
